* {
  box-sizing: border-box;
}

body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #212121;
  background-color: #fafafa;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen-Sans, Ubuntu, Cantarell, Helvetica Neue, sans-serif;
  line-height: 1.5;
}

img {
  max-width: 100%;
  display: block;
}

[data-start], [data-stop] {
  width: 145px;
  height: 40px;
  letter-spacing: .04em;
  text-align: center;
  color: #3b3b3b;
  cursor: pointer;
  background-color: rgba(128, 203, 196, .6);
  border: 1px solid #3b3b3b;
  border-radius: 5px;
  font-size: 18px;
  transition: all .25s;
}

[data-start]:hover, [data-stop]:hover {
  background-color: #80cbc4;
}

[data-start]:focus, [data-stop]:focus {
  background-color: #80cbc4;
  border: 3px solid #fff;
  outline: 3px solid #80cbc4;
}

[data-start]:disabled, [data-stop]:disabled {
  color: rgba(0, 0, 0, .3);
  opacity: .8;
  cursor: not-allowed;
  background-color: rgba(59, 59, 59, .3);
  border: 1px solid rgba(0, 0, 0, .3);
}

#datetime-picker {
  width: 330px;
  height: 40px;
  letter-spacing: .04em;
  text-align: center;
  color: #3b3b3b;
  background-color: rgba(128, 203, 196, .6);
  border: none;
  border-radius: 5px;
  margin-bottom: 20px;
  margin-right: 20px;
  font-size: 18px;
  transition: all .25s;
}

#datetime-picker:focus {
  background-color: #80cbc4;
  border: 3px solid #fff;
  outline: 3px solid #80cbc4;
}

.timer {
  width: 600px;
  letter-spacing: .04em;
  color: #3b3b3b;
  background-color: rgba(128, 203, 196, .6);
  border-radius: 5px;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  gap: 20px;
  padding: 20px 30px;
  font-size: 18px;
  display: flex;
}

.value {
  background-color: rgba(128, 203, 196, .6);
  border-radius: 5px;
  padding: 3px;
  font-size: 24px;
  position: relative;
}

.form {
  flex-direction: column;
  justify-content: center;
  display: flex;
}

.form label {
  width: 350px;
  height: 40px;
  letter-spacing: .04em;
  text-align: center;
  color: #3b3b3b;
  background-color: rgba(128, 203, 196, .6);
  border: none;
  border-radius: 5px;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 10px;
  padding: 5px;
  font-size: 18px;
  transition: all .25s;
  display: flex;
}

.form input {
  width: 180px;
  height: 100%;
  letter-spacing: .04em;
  text-align: right;
  color: #3b3b3b;
  background-color: rgba(255, 255, 255, .6);
  border: none;
  border-radius: 5px;
  padding: 5px;
  font-size: 18px;
  transition: all .25s;
}

.form input:hover {
  background-color: #fff;
}

.form input:focus {
  background-color: #fff;
  border: 4px solid rgba(128, 203, 196, .6);
  outline: 3px solid #fff;
}

.form button {
  width: 350px;
  height: 40px;
  letter-spacing: .04em;
  text-align: center;
  color: #3b3b3b;
  cursor: pointer;
  background-color: rgba(128, 203, 196, .6);
  border: 1px solid #3b3b3b;
  border-radius: 5px;
  font-size: 18px;
  transition: all .25s;
}

.form button:hover {
  background-color: #80cbc4;
}

.form button:focus {
  background-color: #80cbc4;
  border: 3px solid #fff;
  outline: 3px solid #80cbc4;
}

